body {
  font-family: var(--font-body);
  background: var(--color-cream-50);
  color: var(--color-charcoal-700);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  scroll-behavior: smooth;
}
::selection {
  background: var(--color-saffron-200);
  color: var(--color-charcoal-800);
}
:focus-visible {
  outline: 2px solid var(--color-saffron-400);
  outline-offset: 2px;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(24px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(24px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes pageFlip {
  0% {
    transform: rotateY(0deg);
    opacity: 1;
  }
  50% {
    transform: rotateY(-90deg);
    opacity: 0;
  }
  51% {
    transform: rotateY(90deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}
.animate-fade-in-up {
  animation: fadeInUp 0.6s var(--ease-elegant) both;
}
.animate-fade-in {
  animation: fadeIn 0.5s var(--ease-smooth) both;
}
.animate-slide-in-right {
  animation: slideInRight 0.6s var(--ease-elegant) both;
}
.animate-scale-in {
  animation: scaleIn 0.4s var(--ease-elegant) both;
}
.animate-page-flip {
  animation: pageFlip 0.6s var(--ease-elegant) both;
}
.delay-100 {
  animation-delay: 100ms;
}
.delay-200 {
  animation-delay: 200ms;
}
.delay-300 {
  animation-delay: 300ms;
}
.delay-400 {
  animation-delay: 400ms;
}
.delay-500 {
  animation-delay: 500ms;
}
.delay-600 {
  animation-delay: 600ms;
}
.delay-700 {
  animation-delay: 700ms;
}
@media (prefers-reduced-motion: reduce) {
  .animate-fade-in-up,
  .animate-fade-in,
  .animate-slide-in-right,
  .animate-scale-in,
  .animate-page-flip {
    animation: none;
    opacity: 1;
    transform: none;
  }
}
